body{
  background-color: aqua;
}

.App{
  background-color:cadetblue;
  min-height: 500px;
  width:400px;
  margin:40px auto;
}

#todo-form input{
  background-color: chartreuse;
  border:0;
  width:200px;
  height:50px;
  padding:0 20px;
  margin:20px;
}

#todo-form input::placeholder{
  color:darkgray;
}

#todo-form input:focus,#todo-form button:focus{
  outline:none;
}

#todo-form button{
  height: 50px;
  width:80px;
  border-radius: 5px;
  border: 0;
  background-color: darkcyan;
  cursor: pointer;
  font-weight: bolder;
  color: white;
}
